<template>
	<div ref="trys" style="height: 100%">
	  <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail"></info>
      </div>
   	  <div  v-show="!showInfo"  class="all-container">
   	  	<!--<div class="list-header noborder-ListHeader">
   	  		<i class="hasIcon hasImg">
                <img src="dist/img/file/base-infor-icon.png" />
             </i>
   	  		充电设备列表
   	  	</div>-->
        <div class="operate-div operateBtn">
            <button class="button-operate" @click="addEquipment" v-if="controls[1]">
                   <i class="hasIcon hasImg">
                      <icon name="plus-circle" scale="1.4"></icon>
                   </i>添加
            </button>
            <button class="button-operate" @click="removeEquipment" v-if="controls[3]">
                   <i class="hasIcon hasImg">
                       <icon name="trash" scale="1.4"></icon>
                   </i>删除
             </button>
           
        </div>
   	  	<div class="operate-div">
            <template v-for="(item, $index) in formList">
                <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
               <!--  <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"    type= "doubleDate"  :dateOpt="item.options"
                       @getDatePicker="getFrom"></datePicker> -->
            </template>
         </div>
         <div ref="myOwnTable" class="table-container" >
            <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow"
                    @rowDbClick="checkInfo"></tables>
         </div>
   	  </div>
   	  <dialogs type="customer" ref="addEquip" v-if="showDialog" width="45em" :hasNoPaddB='true'>
        <div slot="header">
          添加设备
        </div>
        <div slot="content" >
          <ul class="dialog-ul">
            <li v-for="(item,$index) in addList" class=" formList " :class="item.classes">
              <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
              <label v-if="item.isEmpty" class="label-show" style="margin-left: 0;"><span >&nbsp;</span>{{item.label}} </label>
              <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
              <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
              <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
            </li>
            <!-- <li class="allWidth hasCheck-allWidth">
            	<input type="checkbox"  class="checkBox"  style="float:left;margin-left:5.5em"  v-model="formData.hasQRCode" />
                <span class="inputChoose  myCheckbox">创建同时生成二维码</span>
            </li> -->
          </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitDevice">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
      <dialogs type="confirm" ref="conformDialog"></dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
  
   </div>
</template>

<script>
   import 'vue-awesome/icons/plus-circle'
   import info from './info.vue'
   export default{
     data() {
       return {
         showInfo: false,
         selected: [],
         showDialog: true,
         searchParam: {
            deviceNo:'',
            endDate: '',
            startDate: ''
         },
         row: '',
         controls: [],
         tableOpt: {
            column: [
              {field: 'state',type: 'checkBox', textAlign: 'center',  width: '30'},
              {title: '设备编号', field: 'deviceNo',textAlign: 'center',width: '130'},
              {title: '端口1', field: 'runState1', textAlign: 'center', width: '90', type: 'string', formatter: function (val, index, row) {
                if(row.online != 1) {
                  return '<div class="orange">离线</div>'
                } else {
                  let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                  switch(vals) {
                    case '0':
                      return '<div>空闲</div>'
                    case '1':
                      return '<div class="green">充电</div>'
                    case '2':
                      return '<div style="color:rgba(255,255,255,0.6)">停用</div>'
                    case '-1':
                       return '<div class="red">故障</div>'
                    default:
                      return '<div >--</div>'
                  }
                }
                
              }},
              {title: '端口2', field: 'runState2', textAlign: 'center', width: '90', type: 'string', formatter: function (val, index, row) {
                if(row.online != 1) {
                  return '<div class="orange">离线</div>'
                } else {
                  let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                  switch(vals) {
                    case '0':
                      return '<div>空闲</div>'
                    case '1':
                      return '<div class="green">充电</div>'
                    case '2':
                      return '<div style="color:rgba(255,255,255,0.6)">停用</div>'
                    case '-1':
                       return '<div class="red">故障</div>'
                    default:
                      return '<div >--</div>'
                  }
                }
              }},
              {title: '通信方式', field: 'comMethodName', textAlign: 'center', width: '100'},
              {title: '通信编号', field: 'commNo', textAlign: 'center', width: '120'},
              {title: '安装时间', field: 'installDate', textAlign: 'center', width: '130'},
              {title: '安装地址', field: 'installAddr', textAlign: 'center', width: '250'},
              {title: '备注', field: 'remark', textAlign: 'center', width: '250'}
            ],
            url: 'charger/deviceList',
            method: 'post',
           // method: 'get',
           // url: 'dist/json/deviceMana.json',
            pageSize: 20,
            pageNumber: 1,
            pageList: [20, 40, 60],
            queryParam: {},
            isExport: true,
            exportURL: 'charger/deviceListDown'
        },
        tableShow: false,
        searchDate: '',
        formData: {
           remark: '',
           deviceNo: '',
           installDate: '',
           comMethod: '',
           commNo: '',
           installAddr: '',
           transformerNo: '',
           meterNo: ''
        }
       }
     },
     components: {
         info
    },
     computed: {
     	formList: function () {
     		let dates = this.initDate(true);
            return [
               {
                 type: 'timer',
                 options: {
                   key: 'searchDate',
                   value: {startDate: dates.start, endDate: dates.end},
                   width: '23em',
                   fontSize: '0.8em'
                 }
               },
               {
                 type: 'input',
                 options: {
                   type: 'typeSearch',
                   key: 'searchParam',
                   selectOpt: {
                     width: '7em',
                     cs: 'client',
                     data: [
                      {text: '设备编号', value: 'deviceNo'}
                     ]
                   }
                 }
               }
            ]
     	},
     	addList: function () {
     		let width = 'calc(100% - 6.5em)';
            return [
                {  
                	classes: 'halfWidth',
                	type: 'input', 
                	isEmpty: false, 
                	label: '设备编号', 
                	options: {
                		type: 'normalInput', 
                		width: width,  
                		limit: 'number',
                		placeHolder: '1-7为数字', 
                    value: this.formData.deviceNo,
                		length: 7,
                		key: 'formData.deviceNo'
                    }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'timer', 
                  isEmpty: false, 
                  label: '安装时间', 
                  options: {
                    key: 'formData.installDate', 
                    value: this.formData.installDate,
                    width: width
                   }
                },
                {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '变压器号', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    placeHolder: '1-20个字符', 
                    length: 20,
                    key: 'formData.transformerNo',
                    value: this.formData.transformerNo
                  }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: false, 
                  label: '上级总表', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    limit: 'number',
                    placeHolder: '1-12为数字', 
                    value: this.formData.meterNo,
                    length: 12,
                    key: 'formData.meterNo'
                    }
                },
               
                {  
                	classes: 'halfWidth',
                	type: 'select', 
                	isEmpty: false, 
                	label: '通信方式', 
                	options: {
                		cs: 'server',
                		method: 'post',
                		url: 'dictItem/queryByDictCode',
                		width: width,  
                		param: {dictCode: '17'},
                    constant: this.formData.comMethod == '' ? '' : {dictItemValue: this.formData.comMethod},
                		text: 'dictItemName',
                		value: 'dictItemValue',
                		key: 'formData.comMethod'
                   }
                },
              
                {  
                	classes: 'halfWidth hasMarginLi',
                	type: 'input', 
                	isEmpty: false, 
                	label: '通信编号', 
                	options: {
                		type: 'normalInput', 
                		placeHolder: '1-30位（数字和字母组合）', 
                    value: this.formData.commNo,
                		width: width,  
                		limit: 'NAC',
                		length: 30,
                		key: 'formData.commNo'
                   }
                },
               
                {  
                	classes: 'allWidth',
                	type: 'input', 
                	isEmpty: false, 
                	label: '安装地址', 
                	options: {
                		type: 'textArea', 
                		width: width,  
                    value: this.formData.installAddr,
                		placeHolder: '200个字符内', 
                		key: 'formData.installAddr'
                   }
                },
                {  
                	classes: 'allWidth',
                	type: 'input', 
                	isEmpty: true, 
                	label: '备注信息', 
                	options: {
                		type: 'textArea', 
                    value: this.formData.remark,
                		width: width,  
                		placeHolder: '200个字符内', 
                		key: 'formData.remark'
                   }
                }
            ]
     	},
     },
  
     methods: {
     	  checkInfo: function (val) {
          this.row = val.val;
          this.showInfo = true;
       	},
     	  closeDiv: function () {
            this.showInfo = false;
            this.getParams();
        },
        cancelDialog: function ()  {
            this.showDialog = false;
            this.formData = this.cleanSearchData(this.formData);
            this.$refs.addEquip.close()
        },
     	 //添加设备
        addEquipment: function () {
            this.showDialog = true;
            let that = this;
            setTimeout(function () {
            	that.$refs.addEquip.show()
            }, 0)
        },
            //删除设备
        removeEquipment: function () {
            let that = this;
            this.getConfirm({
            	emptyMsg: '请先选择要删除的设备!',
            	confirmMsg: '确定要对选中的设备进行删除操作吗？',
            	callback: {
            		confirm: function () {
            			that.deleteDevice();
            		}
            	}
            });
        },
        submitDevice: function () {
           let that = this;
           that.regularTest({  
              emptyKey: ['deviceNo', 'commNo', 'installAddr', 'transformerNo', 'meterNo'],
              prefix: 'formData',
              callback: function () {
                  that.getRequest({
                    url: 'charger/addDevice',
                    method: 'post',
                    param: that.formData,
                    success: function (data) {
                       that.$refs.alerts.show({
                          msg: data.message
                       });
                     //  that.cancelDialog();
                       that.formData = that.cleanSearchData(that.formData);
                       that.getParams(1);
                    },
                    error: function (data) {
                       that.$refs.alerts.show({
                          msg: data.message
                       });
                    }
                  })
              }
            })
        },
        deleteDevice: function () {
            let that = this;
            this.deleteData({
            	url: 'charger/deleteDevice',
            	params: ['deviceNo'],
            	callback: function (data) {
            	
                that.getParams(1);
            	},
            	error: function (data) {
            		 that.$refs.alerts.show({
                    msg: data.message
                 });
            	}
            })
        },
     	getFrom: function (val) {
     	  if (val.combine) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
            }
     	},
     	getParams: function (pageNumber) {
          let arr = this.getSearchData(this.searchParam);
          this.getHeight();
          this.searchTable(arr, pageNumber);
     	},
     	resizeCon: function () {
          this.getHeight();
          if (this.$refs.infoDetail) {
              let info = this.$refs.infoDetail;
              info.lineV !== ''?  info.lineV.resize() : '';
              info.lineA !== ''?  info.lineA.resize() : '';
              info.lineR !== ''?  info.lineR.resize() : '';
            }
     	},
     	windowChange: function () {
          this.resizeCon();
          // let that = this;
          // window.onresize = function () {
          //   that.resizeCon();
          // }
        },
      init: function () {
          let that = this;
          this.tableOpt.isExport = this.controls[6];
          this.tableShow = true;
          // window.onresize = function () {
          //    that.windowChange()
          // }
      },
      stopAccs: function () {
          let that = this;
          that.getWebsocket({
            keyMsg: 'onOffDevice',
            funMsg: function (data) {
                let datas = data.data.status;
                if(datas == 1) {
                   that.getParams();
                }
            }
        })
      },
      deviceUpdate: function () {
          let that = this;
          that.getWebsocket({
            keyMsg: 'monitorDeviceList',
            sign: true,
            funMsg: function (data) {
              that.getParams();
            }
          });
     },
    },
     created: function () {
         this.isIn();
         this.getAction();

     },
     beforeRouteEnter(to, from, next) {
        next(vm => { 
            if(vm.showInfo) {
              let obj = vm.$refs.infoDetail;
              obj.getDeviceInfo();
              obj.deviceUpdate();
              obj.stopAcc();
            } else {
              vm.getParams();
              vm.stopAccs();
             // vm.deviceUpdate();
            }
            
       });
     },
     watch: {
     	searchDate(val) {
     	  let arr = val.split('至');
     	  this.searchParam.startDate = arr[0];
     	  this.searchParam.endDate = arr[1];
     	  this.getParams(1);
     	}
     }
    }
</script>

<style lang="scss">
  .dialog-ul .hasCheck-allWidth {
    width: fit-content!important;
    width:  -webkit-fit-content!important;
    width:  -moz-fit-content!important;
    width:max-content;
    float: left;
    .inputChoose{
        line-height: 2.5;
        padding-left: 1em;
    }
    input.checkBox{
    	left:0.7em
    }
  }
</style>


