<template>
    <div  class="info-div" style="100%">
        <div class="info-header ">
          <span class="callback" @click="resetComponent">充电设备管理 </span>
          <span> > 设备详情</span>
        </div>
        <div class="info-container" >
            <div  class="list-content detail-content  list-contentB" >
              <div class="list-header " style="border:0">
                 <i class="hasIcon hasImg">
                  <img src="dist/img/widsomApp/basic.png" />
                </i>基础信息
                 <button class="list-operate" @click="getHeart">
                    <i class="hasIcon ">
                      <icon name="headphones" ></icon>
                    </i>读取心跳
                 </button>
                 <button class="list-operate" @click="setHeart" >
                    <i class="hasIcon ">
                      <icon name="pencil-square" ></icon>
                    </i>设置心跳
                 </button>
                <button class="list-operate " @click="deleteDevice" v-if="controls[3]">
                    <i class="hasIcon ">
                      <icon name="trash" ></icon>
                    </i>删除
                </button>
                <button class="list-operate" @click="editDevice" v-if="controls[2]">
                    <i class="hasIcon ">
                      <icon name="edit" ></icon>
                    </i>编辑
                 </button>
                
              </div>
              <ul class="form-list" style="border-bottom:1px solid rgba(255,255,255,0.3);padding:1em">
                     <li v-for="(item,$index) in commInfo" style="margin:0!important">
                         <span class="label-span">{{item.text}}</span>
                         <span class="label-span" style="max-width: calc(100% - 8em);"> &nbsp;:&nbsp;&nbsp;{{item.value}}</span>
                     </li>
              </ul>
              <div class="list-header" style="border:0;margin-top:1em">
                 <i class="hasIcon hasImg">
                  <img src="dist/img/face/QR.png" />
                </i>设备二维码
              </div>
              <div class="chartCon" >
                <div class="device_port" v-for="(item, $index) in ports">
                  <ul class="form-list">
                    <li class="halfWidth" >
                       <span class="label-span" >编号 : &nbsp;&nbsp;</span>
                       <span class="label-span" >{{deviceCommInfo.deviceNo}}{{item.port}}</span>
                    </li>
                     <li class="halfWidth" >
                       <span class="label-span" >端口 : &nbsp;&nbsp;</span>
                       <span class="label-span" >{{item.port}}</span>
                    </li>
                    <li class="halfWidth" >
                       <span class="label-span" >状态 : &nbsp;&nbsp;</span>
                       <span  class="label-span" >{{getPortStatus(item.runkey)}}</span>
                    </li>
                    <li class="halfWidth" >
                       <span class="label-span" >过流 : &nbsp;&nbsp;</span>
                       <span  class="label-span" >{{item.overCurrent}} {{!!item.overCurrent ? '(A)' : ''}}</span>
                    </li>
                   
                
                    <li class="halfWidth" >
                       <span class="label-span" >过压 : &nbsp;&nbsp;</span>
                       <span  class="label-span" >{{item.overVoltage}} {{!!item.overVoltage ? '(V)' : ''}}</span>
                    </li>
                    <li class="halfWidth" >
                       <span class="label-span" >涓流 : &nbsp;&nbsp;</span>
                       <span  class="label-span" >{{item.underCurrent}} {{!!item.underCurrent ? '(A)' : ''}}</span>
                    </li>
                    <li class="halfWidth" >
                       <span class="label-span" >欠压 : &nbsp;&nbsp;</span>
                       <span  class="label-span" >{{item.underVoltage}} {{!!item.underVoltage ? '(V)' : ''}}</span>
                    </li>
                    <li class="halfWidth" >
                       <span class="label-span" >涓流时延 : &nbsp;&nbsp;</span>
                       <span  class="label-span" >{{item.underCurrentDelay}}</span>
                    </li>
                   
                    
                    <li class="allWidth">
                        <span class="label-span" >参数 : &nbsp;&nbsp;</span>
                       <button class="list-operate" @click="getDeviceParams(item.port)" >
                          <i class="hasIcon ">
                            <icon name="arrow-circle-down" scale="1"></icon>
                         </i>读取
                       </button>
                       <button class="list-operate "  @click="setDeviceParams(item.port)" >
                          <i class="hasIcon ">
                             <icon name="arrow-circle-up" scale="1"></icon>
                          </i>设置
                       </button>
                    </li>
                    <li class="allWidth">
                        <span class="label-span" >操作 : &nbsp;&nbsp;</span>
                       <button class="list-operate" @click="disabledDevice(item.port)" v-if="controls[40]">
                          <i class="hasIcon ">
                            <icon name="ban" scale="1"></icon>
                         </i>停用
                       </button>
                       <button class="list-operate "  @click="runDevice(item.port)" v-if="controls[41]">
                          <i class="hasIcon ">
                             <icon name="plug" scale="1"></icon>
                          </i>启用
                       </button>

                    
                    </li>
                   <!--  <li class="allWidth">
                        <button class="button-operate "  @click="runDevice(item.port)" v-if="controls[41]">
                          <i class="hasIcon ">
                             <icon name="play-circle" scale="1.4"></icon>
                          </i>启用
                       </button>
                    </li> -->
                  </ul>
                  <div class="hasQRs">
                     <img  class="QRCanva" :src="item.QR">
                    <!--  <div class="QRD">
                        微信&nbsp;
                        <span>“扫一扫”</span>
                     </div> -->
                     <div class="QRD">
                        {{deviceCommInfo.deviceNo}}{{item.port}}
                     </div>
                  </div>
                </div>
              </div>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="confirm" ref="confirms"></dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
        <dialogs type="customer" ref="editEquip"  width="45em" :hasNoPaddB='true'>
           <div slot="header">
              编辑设备
           </div>
           <div slot="content" >
             <ul class="dialog-ul">
               <li v-for="(item,$index) in editList" class=" formList " :class="item.classes">
                  <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
                  <label v-if="item.isEmpty" class="label-show" style="margin-left: 0;"><span >&nbsp;</span>{{item.label}} </label>
                  <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
                  <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
                  <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
               </li>
            </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitEdit">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
       <dialogs type="customer" ref="editParam"  width="38em" :hasNoPaddB='true'>
           <div slot="header">
              参数配置 ({{deviceCommInfo.deviceNo + currentPort}})
           </div>
           <div slot="content" >
             <ul class="dialog-ul">
               <li v-for="(item,$index) in editParam" class=" formList " :class="item.classes">
                  <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
                  <label v-if="item.isEmpty" class="label-show" style="margin-left: 0;"><span >&nbsp;</span>{{item.label}} </label>
                  <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
                  <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
                  <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
               </li>
            </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitEditParam">提交</button>
          <button class="button-default cancel-button" @click="cancelEditParam">取消</button>
        </div>
      </dialogs>
      <dialogs type="customer" ref="editHearts"  width="30em" >
           <div slot="header">
              设置心跳
           </div>
           <div slot="content" >
             <ul class="dialog-ul">
               <li v-for="(item,$index) in editHerat" class=" formList " :class="item.classes">
                  <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
                  <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom"  @enter="submitEditHeart" :ref="item.options.key"></inputs>
               </li>
            </ul>
        </div>
      <!--   <div slot="footer">
          <button class="button-default" @click="submitEditHeart">提交</button>
          <button class="button-default cancel-button" @click="cancelEditHeart">取消</button>
        </div> -->
      </dialogs>
    </div>
</template>
<script>
    import QrcodeVue from 'qrcode.vue'
    import 'vue-awesome/icons/ban'
    import 'vue-awesome/icons/plug'
    
    import 'vue-awesome/icons/play-circle'
    import 'vue-awesome/icons/arrow-circle-down'
    import 'vue-awesome/icons/arrow-circle-up'
    import 'vue-awesome/icons/headphones'
    import 'vue-awesome/icons/pencil-square'
    export default{
        data() {
            return {
               accountData: {
                 ramainTime: '11',
                 current: '22',
                 voltage: '333',
                 power: '53'
               },
               deviceCommInfo: {
                  remark: '',
                  deviceNo: '',
                  installDate: '',
                  projectGuid: '',
                  commNo: '',
                  runState1:'',
                  runState2: '',
                  installAddr: '',
                  projectName:'',
                  price: '',
                  heartStep: ''
               },
               rangeData: {
                   overCurrent: '', overVoltage: '', underVoltage: '', underCurrent: '', underCurrentDelay: ''
               },
             
               ports: [
                 {port: 1, QR: '', runkey: 'runState1', overCurrent: '', overVoltage: '',underVoltage: '', underCurrent: '', underCurrentDelay: ''},
                 {port: 2, QR: '', runkey: 'runState2', overCurrent: '', overVoltage: '', underVoltage: '', underCurrent: '', underCurrentDelay: ''}
               ],
               currentPort: '',
               heartData: ''
            }
        },
        components: {
          QrcodeVue
        },
        props: ['row', 'controls'],
        methods: {
            setHeart: function () {
              let that = this;
              that.$refs.editHearts.show({
                 hasClose: true,
                 action: {
                   cancel: function () {
                      that.cancelEditHeart()
                   }
                 }
              });
            },
            cancelEditHeart: function () {
              this.heartData = '';
              this.$refs.editHearts.close();
            },
            submitEditHeart: function (val) {
                let that = this;
                if(val.val.toString() == '') {
                    that.$refs.alerts.show({
                        msg: '设备心跳不能为空！'
                    });
                    return
                }
                if(parseInt(val.val) < 1 || parseInt(val.val) > 600) {
                    that.$refs.alerts.show({
                        msg: '设备心跳值最大不超过600秒，且不能为0！'
                    });
                    return
                }
                that.getOption(val, that);
                that.regularTest({
                   emptyKey: ['heartData'],
                   prefix: '',
                   callback: function () { 
                      that.getRequest({
                        url: 'charger/setHeartStep',
                        method: 'post',
                        param: {
                           commNo: that.row.commNo,
                           heartStep: that.heartData
                        },
                        success: function (data) {
                           that.$refs.loadings.show({msg: '正在设置心跳，请稍后。。。', hasClose: true});
                           that.getSetHeartBack()
                        },
                       error: function (data) {
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                       }
                    })
                 }
              })
            },
            getSetHeartBack: function () {
               let that = this;
               that.getWebsocket({
                 keyMsg: 'setHeartStepRes',
                 sign: true,
                 funMsg: function (data) {
                   if(data.data.commNo == that.row.commNo) {
                     that.$refs.loadings.close();
                     let state = data.data.status;
                     let msg = '';
                     if(state == 1) {
                        msg = '设置心跳成功！';
                        that.$set(that.deviceCommInfo, 'heartStep', that.heartData);
                        that.cancelEditHeart();
                     } else {
                       msg = state == 0 ? '设置心跳失败，请稍后重试！' : '设备不在线';
                     }
                      that.$refs.alerts.show({msg: msg});
                      that.$store.state.callback['setHeartStepRes'] = null;
                      that.$store.state.linkCheck = 'linkCheck';
                    }
                  }
               })
            },
            getHeart: function () {
               let that = this;
               that.getRequest({
                url: 'charger/queryHeartStep',
                method: 'post',
                param: {commNo: that.row.commNo},
                success: function (data) {
                   that.$refs.loadings.show({msg: '正在读取心跳，请稍后。。。', hasClose: true});
                   that.getHeartDate();
                }
              }, true)
            },
            getHeartDate: function () {
              let that = this;
              that.getWebsocket({
                 keyMsg: 'chargerHeartStep',
                 sign: true,
                 funMsg: function (data) {
                    if(data.data.commNo == that.row.commNo) {
                        that.$refs.loadings.close();
                        let state = data.data.status;
                        let msg = '';
                        if(state == 1) {
                          msg = `读取心跳成功，当前心跳值为${data.data.heartStep.toString()}秒， 是否进行修改？`;
                          that.$set(that.deviceCommInfo, 'heartStep', data.data.heartStep.toString());
                          that.$refs.confirms.show({
                            msg: msg,
                            action:{
                              confirm: function () {
                                that.setHeart()
                              }
                            }
                          })
                        } else {
                          msg = state == 0 ? '读取心跳失败，请稍后重试！' : '设备不在线';
                          that.$refs.alerts.show({msg: msg});
                        }
                        that.$store.state.callback['chargerHeartStep'] = null;
                        that.$store.state.linkCheck = 'linkCheck';
                    }
                  }
               })
            },
            getDeviceParams: function (port) {
              let that = this;
              that.getRequest({
                url: 'charger/queryThreshold',
                method: 'post',
                param: {commNo: that.row.commNo, port: port},
                success: function (data) {
                   that.$refs.loadings.show({msg: '正在读取参数，请稍后。。。', hasClose: true});
                   that.getRangeDate(port);
                }
              }, true)
            },
            getRangeDate: function (port) {
               let that = this;
               that.getWebsocket({
                 keyMsg: 'chargerThreshold',
                 sign: true,
                 funMsg: function (data) {
                    if(data.data.commNo == that.row.commNo && data.data.port == port) {
                       that.$refs.loadings.close();
                       let state = data.data.status;
                       let msg = '';
                       if(state == 1) {
                           msg = '读取参数成功！';
                           let datas = data.data;
                           for(let key in datas) {
                             if(key != 'status' && key != 'port') {
                               that.ports[parseInt(port) - 1][key] = datas[key];
                             }
                           }
                        } else {
                          msg = state == 0 ? '读取参数失败，请稍后重试！' : '设备不在线';
                       }
                       that.$refs.alerts.show({msg: msg});
                       that.$store.state.callback['chargerThreshold'] = null;
                       that.$store.state.linkCheck = 'linkCheck';
                    }
                  }
               })
            },
            setDeviceParams: function (port) {
               this.currentPort = port;
               this.$refs.editParam.show(); 
            },
            cancelEditParam: function () {
               this.currentPort = '';
               this.rangeData = this.cleanSearchData(this.rangeData);
               this.$refs.editParam.close(); 
            },
            getPortStatus: function (val) {
              if(this.deviceCommInfo.online != 1) {
                return '离线'
              } 
              return this.getEnumItem('deviceStatus', this.deviceCommInfo[val]).text
            },
            getFrom: function (val) {
               this.getOption(val, true);
            },
            submitEditParam: function () {
                let that = this;
                let alerts = this.$refs.alerts;
                that.regularTest({
                   emptyKey: ['overCurrent', 'overVoltage','underVoltage', 'underCurrent', 'underCurrentDelay'],
                   prefix: 'rangeData',
                   callback: function () { 
                      let oc = parseFloat(that.rangeData.overCurrent);
                      let ov = parseFloat(that.rangeData.overVoltage);
                      let uv = parseFloat(that.rangeData.underVoltage);
                      let uc = parseFloat(that.rangeData.underCurrent);
                      if(oc > 5 || oc < 2) {
                        alerts.show({msg: '过流阈值的取值范围为2-5之间， 允许保留两位小数'});
                        return
                      }
                      if(ov > 280 || ov < 250) {
                        alerts.show({msg: '过压阈值的取值范围为250-280之间， 允许保留两位小数'});
                        return
                      }
                      if(uv > 220 || uv < 120) {
                        alerts.show({msg: '欠压阈值的取值范围为120-220之间， 允许保留两位小数'});
                        return
                      }
                      if(uc > 0.5 || uc < 0.1) {
                        alerts.show({msg: '涓流阈值的取值范围为0.1-0.5之间， 允许保留两位小数'});
                        return
                      }
                      let param = that.rangeData;
                      param['commNo'] = that.row.commNo;
                      param['port'] = that.currentPort;
                      that.getRequest({
                        url: 'charger/setThreshold',
                        method: 'post',
                        param: param,
                        success: function (data) {
                          that.$refs.loadings.show({msg: '正在设置参数，请稍后。。。', hasClose: true});
                          that.waitForSetBack(that.currentPort);
                        },
                       error: function (data) {
                         alerts.show({
                             msg: data.message
                          });
                       }
                      })
                 }
              })
            },
            waitForSetBack: function (port) {
               let that = this;
               that.getWebsocket({
                 keyMsg: 'setThresholdRes',
                 sign: true,
                 funMsg: function (data) {
                    if(data.data.commNo == that.row.commNo && data.data.port == port) {
                        that.$refs.loadings.close();
                        let state = data.data.status;
                        let msg = '';
                        if(state == 1) {
                            msg = '设置参数成功！';
                            for(let key in that.rangeData) {
                                that.$set(that.ports[parseInt(that.currentPort) - 1], key, that.rangeData[key])
                            }
                            that.cancelEditParam();
                        } else {
                          msg = state == 0 ? '设置参数失败，请稍后重试！' : '设备不在线';
                        }
                        that.$refs.alerts.show({msg: msg});
                        that.$store.state.callback['setThresholdRes'] = null;
                        that.$store.state.linkCheck = 'linkCheck';
                      }
                  }
               })
            },
            submitEdit: function () {
                let that = this;
                that.regularTest({
                   emptyKey: ['commNo', 'installAddr','transformerNo', 'meterNo'],
                   prefix: 'deviceCommInfo',
                   callback: function () { 
                      that.getRequest({
                        url: 'charger/editDevice',
                        method: 'post',
                        param: that.deviceCommInfo,
                        success: function (data) {
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                         that.cancelDialog();
                         //that.resetComponent();
                        },
                       error: function (data) {
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                       }
                    })
                 }
              })
            },
            cancelDialog: function () {
              this.$refs.editEquip.close();
              this.getDeviceInfo();
            },
            editDevice: function () {
              this.$refs.editEquip.show();
            },
            operateDevice: function (confirmMsg, operateUrl, param, waitSocket, flag) {
               let that = this;
               let alerts = this.$refs.alerts;

               this.confirms.show({
                  msg: confirmMsg,
                  action: {
                     confirm: function () {
                         that.getRequest({
                             url: operateUrl,
                             param: param, 
                             method: 'post',
                             success: function (data) {
                               if(waitSocket) {
                                   that.$refs.loadings.show({msg: '正在停用设备，请稍后。。。', hasClose: true});
                                   that.stopDeviceAcc(param)
                               } else {
                                   alerts.show({msg: data.message});
                                   setTimeout(function () {
                                      if(flag == 'return') {
                                         that.resetComponent();
                                      } else {
                                         that.getDeviceInfo();
                                      }
                                    
                                   }, 600)
                               }
                             },
                             error: function (data) {
                               alerts.show({msg: data.message})
                             }
                          })
                     }
                  }
               })
            },
            stopDeviceAcc: function (param) {
               let that = this;
               let alerts = this.$refs.alerts;
               that.getWebsocket({
                 keyMsg: 'onOffDevice',
                 funMsg: function (data) {
                    if(data.deviceNo == that.row.deviceNo && data.port == param.port) {
                       let datas = data.data.status;
                       let msg = '';
                       if(datas == 1) {
                            msg = '停用操作成功';
                            that.$set(that.deviceCommInfo, 'runState', data.data.runState);
                            that.stopAcc();
                        } else if(datas == 0) {
                            msg = '停用操作失败'
                        } else if(datas == -1) {
                           msg = '设备不在线'
                        }
                        that.$refs.loadings.close();
                        alerts.show({msg: msg});
                        that.$store.state.callback['onOffDevice'] = null;
                        that.$store.state.linkCheck = 'linkCheck';
                    }
                 }
               });
            },
            stopAcc: function () {
               let that = this;
               that.getWebsocket({
                 keyMsg: 'onOffDevice',
                 sign: true,
                 funMsg: function (data) {
                    let datas = data.data.status;
                    if(datas == 1 && that.row.deviceNo == data.data.deviceNo) {
                         that.getDeviceInfo();
                     }
                 }
               })
            },
            disabledDevice: function (port) {
              let param = {};
              param.commNo = this.row.commNo;
              param.port = port;
              param.onOrOff = 0;
              this.operateDevice('确定要停用该设备吗？', 'charger/offOn', param, false);
            }, 
            deleteDevice: function () {
              let arr = [];
              arr.push(this.row.deviceNo);
              this.operateDevice('确定要删除该设备吗？', 'charger/deleteDevice', arr, false, 'return');
            }, 
           //返回列表
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            getDeviceInfo: function () {
              let that = this;
              that.getRequest({
                url: 'charger/deviceDetial',
                method: 'post',
                param: {deviceNo: that.row.deviceNo},
                success: function (data) {
                   for(let key in data.data) {
                     that.$set(that.deviceCommInfo, key, data.data[key])
                   }
                }
              }, true)
            },
            runDevice: function (port) {
                 let param = {};
                 param.commNo = this.row.commNo;
                 param.port = port;
                 param.onOrOff = 1;
                 this.operateDevice('确定要启用该设备吗？', 'charger/offOn', param, false, 'refresh');
            },
            getQR: function (port) {
              let that = this;
              that.getRequest({
                url: 'charger/getQ',
                resType: 'arraybuffer',
                method: 'post',
                param: {deviceNo: that.row.deviceNo, port: port},
                success: function (data) {
                   let blob = new Blob([data],{type: 'applocation/vnd.ms-excel'});
                   let objUrl = URL.createObjectURL(blob);
                   that.$set(that.ports[port - 1], 'QR' , objUrl)
                }
              }, true, 'arraybuffer')
            },
            deviceUpdate: function () {
               let that = this;
               that.getWebsocket({
                  keyMsg: 'monitorDeviceList',
                  sign: true,
                  funMsg: function (data) {
                     let datas = data.data;
                     if(datas.deviceNo == that.row.deviceNo) {
                         that.getDeviceInfo()
                     }
                  }
               });
           },
          
        },
        computed: {
           editParam() {
              let width = 'calc(100% - 6.5em)';
              if(parseInt(this.currentPort) > 0) {
                 let data = this.ports[parseInt(this.currentPort) - 1];
              return [
                
                {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '过流阈值', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    dataType: '+float',
                    placeHolder: '2-5之间，可保留两位小数', 
                    length: 4,
                    value: data.overCurrent,
                    key: 'rangeData.overCurrent'
                  }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: false, 
                  label: '涓流阈值', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    dataType: '+float',
                    placeHolder: '0.1-0.5之间，可保留两位小数', 
                    length: 4,
                    value: data.underCurrent,
                    key: 'rangeData.underCurrent'
                  }
                },
                {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '过压阈值', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    dataType: '+float',
                    placeHolder: '250-280之间，可保留两位小数', 
                    length: 6,
                    value: data.overVoltage,
                    key: 'rangeData.overVoltage'
                   }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: false, 
                  label: '欠压阈值', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    dataType: '+float',
                    placeHolder: '120-220之间，可保留两位小数', 
                    length: 6,
                    value: data.underVoltage,
                    key: 'rangeData.underVoltage'
                   }
                },
                {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '涓流时延', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    limit: 'number',
                    placeHolder: '整数（单位为秒）', 
                    length: 8,
                    value: data.underCurrentDelay,
                    key: 'rangeData.underCurrentDelay'
                   }
                }
                
              ]
              }
           },
           editHerat() {
              let width = 'calc(100% - 6.5em)';
              return [
                {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '心跳 (秒)', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    limit: 'number',
                    key: 'heartData',
                    data: 3,
                    dataType: 'int',
                    placeHolder: '1-600之间的整数，秒为单位',
                    value: this.deviceCommInfo.heartStep == '' ? this.heartData : this.deviceCommInfo.heartStep
                  }
                }
              ]
           },
           editList: function () {
              let width = 'calc(100% - 6.5em)';
              return [
                {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '设备编号', 
                  options: {
                    type: 'disabledInput', 
                    width: width,  
                    value: this.deviceCommInfo.deviceNo
                  }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'timer', 
                  isEmpty: false, 
                  label: '安装时间', 
                  options: {
                    key: 'deviceCommInfo.installDate', 
                    value: this.deviceCommInfo.installDate,
                    width: width
                   }
                },
                {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '变压器号', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    placeHolder: '1-20个字符', 
                    length: 20,
                    value: this.deviceCommInfo.transformerNo,
                    key: 'deviceCommInfo.transformerNo'
                  }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: false, 
                  label: '上级总表', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    limit: 'number',
                    placeHolder: '1-12为数字', 
                    length: 12,
                    value: this.deviceCommInfo.meterNo,
                    key: 'deviceCommInfo.meterNo'
                  }
                },

                {  
                  classes: 'halfWidth',
                  type: 'select', 
                  isEmpty: false, 
                  label: '通信方式', 
                  options: {
                    cs: 'server',
                    method: 'post',
                    url: 'dictItem/queryByDictCode',
              
                    width: width,  
                    param: {dictCode: '17'},
                    text: 'dictItemName',
                    value: 'dictItemValue',
                    constant: this.deviceCommInfo.comMethod !== '' ? {dictItemValue: this.deviceCommInfo.comMethod} : '',
                    key: 'deviceCommInfo.comMethod'
                   }
                },
               
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: false, 
                  label: '通信编号', 
                  options: {
                    type: 'normalInput', 
                    placeHolder: '1-30位（数字和字母组合）', 
                    width: width,  
                    limit: 'NAC',
                    length: 30,
                    value: this.deviceCommInfo.commNo,
                    key: 'deviceCommInfo.commNo'
                   }
                },
            
                {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '安装地址', 
                  options: {
                    type: 'textArea', 
                    width: width,  
                    placeHolder: '200个字符内', 
                    value: this.deviceCommInfo.installAddr,
                    key: 'deviceCommInfo.installAddr'
                   }
                },
                {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: true, 
                  label: '备注信息', 
                  options: {
                    type: 'textArea', 
                    width: width,  
                    placeHolder: '200个字符内', 
                    value: this.deviceCommInfo.remark,
                    key: 'deviceCommInfo.remark'
                   }
                }
              ]
           },
           confirms: function () {
              return this.$refs.confirms
           },
           qrcodeValue: function () {
              let WbUrl = this.$store.state.socketUrl;
              let urlArr = WbUrl.split(':');
              switch(urlArr[0]) {
                 case 'ws':
                   return WbUrl.replace('ws', 'http') + '/';
                 case 'wss':
                  return WbUrl.replace('ws', 'https') + '/';
              }
          },
          commInfo(){
             return [
                {text: '设备编号', value: this.deviceCommInfo.deviceNo || '--'},
               // {text: '设备状态', value: this.getEnumItem('deviceStatus', this.deviceCommInfo.runState).text},
                {text: '通信方式', value: this.deviceCommInfo.comMethodName || '--'},
                {text: '安装地址', value: this.deviceCommInfo.installAddr || '--'},
                {text: '安装时间', value: this.deviceCommInfo.installDate || '--'},
                {text: '总表表号', value: this.deviceCommInfo.meterNo || '--'},
            //    {text: '端口号', value: this.deviceCommInfo.port || '--'},
                {text: '变压器号', value: this.deviceCommInfo.transformerNo || '--'},
                {text: '通信编号', value: this.deviceCommInfo.commNo || '--'},
                {text: '所属项目', value: this.deviceCommInfo.projectName || '--'},
                {text: '电价', value: (this.deviceCommInfo.price.toString() || '--') + ' (元/度)'},
                {text: '心跳', value: (this.deviceCommInfo.heartStep || '--') + ' (秒)'},
                {text: '备注信息', value: this.deviceCommInfo.remark || '--'}
             ]
          },

        },
        created: function () {
          this.getDeviceInfo();
          this.getQR(1);
          this.getQR(2);
          // this.getRangeDate(1);
          // this.getRangeDate(2);
          this.deviceUpdate();
          this.stopAcc()
        }
    }
</script>
<style lang="scss">
    $QRW: 8em;
  .chartCon{
     display: flex;
     width: 80%;
    .device_port{
       flex: 1;
       display: flex;
       padding:0 2%;
       margin-bottom: 2em;
       .form-list{
          width: 60%;
          margin-left:2em;
          li  {
             min-width: calc(50% - 0.5em - 1em);
             span{
               line-height: 2em;
               min-width: 5em
             }
             span + span{
               max-width: calc(100% - 6em)
             }
             .list-operate{
                font-size: 0.83em;
                padding:0;
                height:1.99em;
                margin:0.1em 1.5em 0 0;
             }
          }
          .button-operate{
            font-size: 0.84em;
            padding: 0.4em 17%;
            margin-left:3em;
            .hasIcon{
                margin-top: -0.25em;
            }
          }
          button.button-run{
             color: #227fdf
          }
          button.button-disabled{
            color:  #E54E3C
          }
          button.button-delete{
             color: #E5883D
          }
          button.button-disabled:hover{
            color:  #f13b25
          }
        }
      .hasQRs{
         background:white;
         border-radius:0.1em;
         width: $QRW;
         height:calc(#{$QRW} + 1.5em);
         margin:1.5em 2em 0.5em 0em;
         .QRCanva{
           padding-top:0em;
           width:$QRW;
           height:$QRW
         }
        .QRD{
           color: black;
           font-size: 0.84em;
           margin-top: -1em;
           span{
             color:#BC5F24 
           } 
         }
         .QRD + .QRD {
           margin-top: 0.5em;
         }
      }
     
    }
  }  
  
</style>



